import { useRef, useEffect } from 'react'
import {Matrix4, Color, SceneRenderer, AssetManager, Downloader, TimeKeeper, ManagedWebGLRenderingContext, AtlasAttachmentLoader,  SkeletonJson, Skeleton, AnimationStateData, AnimationState, Physics, Vector2, ResizeMode} from "@esotericsoftware/spine-webgl"

const SpinePlayer = play => {

    const canvasRef = useRef(null)
    var mvp = new Matrix4();
    var bgColor = new Color(0.3, 0.3, 0.3, 1);
    var  renderer, assetManager;
    var timeKeeper;
    var skeleton;
    var animationState;
    var bounds;
    const spineName = 'plane';
    const spineFile = {
        atlas: spineName + ".atlas",
        json: spineName + ".json",
        skin: "default",
        animation: "animation"
    }
    useEffect(() => {

        const canvas = canvasRef.current;
        const  context = new ManagedWebGLRenderingContext(canvas, { alpha: true, premultipliedAlpha: false  });
        const gl = context.gl;

        renderer = new SceneRenderer(canvas, context);
        assetManager = new AssetManager(context, process.env.PUBLIC_URL+"/assets/");
        assetManager.loadTextureAtlas(spineFile.atlas);
        assetManager.loadText(spineFile.json);
        timeKeeper = new TimeKeeper();
        requestAnimationFrame(load);

        function load() {
            timeKeeper.update();
            if (assetManager.isLoadingComplete()) {
                var atlas = assetManager.get(spineFile.atlas);
                var atlasLoader = new AtlasAttachmentLoader(atlas);

                let skeletonJson = new SkeletonJson(atlasLoader);
                let skeletonData = skeletonJson.readSkeletonData(assetManager.require(spineFile.json));

                // var time = new TimeKeeper()

                skeleton = new Skeleton(skeletonData);
                bounds = calculateSetupPoseBounds(skeleton);
                var stateData = new AnimationStateData(skeleton.data);
                animationState = new AnimationState(stateData);
                stateData.defaultMix = 0;
                animationState.timeScale = 0.5;
                animationState.setAnimation(0, spineFile.animation, true);

                requestAnimationFrame(render);
            } else {
                requestAnimationFrame(load);
            }
        }

        function render() {
            timeKeeper.update();
            var delta = timeKeeper.delta;
            resize();
            animationState.update(delta);
            animationState.apply(skeleton);
            skeleton.updateWorldTransform(Physics.update);

            gl.clearColor(bgColor.r, bgColor.g, bgColor.b, bgColor.a);
            gl.clear(gl.COLOR_BUFFER_BIT);
            // renderer.resize(ResizeMode.Expand);
            // renderer.begin();
            // renderer.drawSkeleton(skeleton, false);
            // renderer.end();

            requestAnimationFrame(render)
        }

        function resize() {
            var w = canvas.clientWidth;
            var h = canvas.clientHeight;
            if (canvas.width != w || canvas.height != h) {
                canvas.width = w;
                canvas.height = h;
            }

            // Calculations to center the skeleton in the canvas.
            var centerX = bounds.offset.x + bounds.size.x / 2;
            var centerY = bounds.offset.y + bounds.size.y / 2;
            var scaleX = bounds.size.x / canvas.width;
            var scaleY = bounds.size.y / canvas.height;
            var scale = Math.max(scaleX, scaleY) * 2;
            if (scale < 1) scale = 1;
            var width = canvas.width * scale;
            var height = canvas.height * scale;

            mvp.ortho2d(centerX - width / 2, centerY - height / 2, width, height);
            gl.viewport(0, 0, canvas.width, canvas.height);
        }

        function calculateSetupPoseBounds(skeleton) {
            skeleton.setToSetupPose();
            skeleton.updateWorldTransform(Physics.update);
            var offset = new Vector2();
            var size = new Vector2();
            skeleton.getBounds(offset, size, []);
            return { offset: offset, size: size };
        }
    }, [])

    return canvasRef
}

export default SpinePlayer
